import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const listData = []
for (let i = 0; i < 23; i++) {
  listData.push({
    contentId: i,
    title: `ant design vue part ${i}`,
    description:
      'Ant Design, a design language for background applications, is refined by Ant UED Team.',
    content: [
      'We supply a series of design principles, practical patterns and high quality design resources (Sketch and Axure), to help people create their product prototypes beautifully and efficiently.'
    ]
  })
}

export default new Vuex.Store({
  state: {
    isLogin: false,
    userData: null,
    listData,
    topicList: []
  },
  getters: {
    getLoginState (state) {
      return state.isLogin
    },
    getListData (state) {
      return state.listData
    },
    getItemForIndex: (state) => (index) => {
      return state.listData[index]
    },
    getUserData: (state) => {
      return state.userData
    },
    getUserId: (state) => {
      return state.userData.user_id
    },
    getUserName: (state) => {
      return state.userData.user_name
    }
  },
  mutations: {
    login (state, userData) {
      state.isLogin = true
      state.userData = userData
    },
    quit (state) {
      state.isLogin = false
      state.userData = null
      localStorage.removeItem('userName')
    },
    insertMessage (state, data) {
      const dataIndex = data.index
      state.listData[dataIndex].content.push(data.message)
    },
    setTopicList (state, data) {
      state.topicList = data
    }
  },
  actions: {
    updataTopicList ({ commit }) {
      axios.get(process.env.VUE_APP_APIPATH + process.env.VUE_APP_GETALLTOPICAPI).then((response) => {
        // console.log(response.data)
        commit('setTopicList', response.data)
      })
    }
  },
  modules: {
  }
})
