    postMessage () {
      if (this.message === '') {
        return
      }
      const newMessage = {
        message: this.message,
        time: moment().format()
      }
      this.messageList.push(newMessage)
      this.message = ''
    },
    deleateItem (id) {
      for (let i = 0; i < this.messageList.length; i++) {
        if (this.messageList[i].id === id) {
          this.messageList.splice(i, 1)
          return
        }
      }
    },
